import Header from "../components/Header";
import InputBox from "../components/InputBox";
import Nav from "../components/Nav";
import { autoResponse } from "../lib/autoResponse";
import {
    queryCheckBodyList,
    querySymptomList,
    queryQuestionOptionList,
    queryType,
    queryDiseaseDetail,
    queryDisease,
    queryMedicineList,
    queryMedicineDetail,
    queryDeptName,
    queryDoctorList
} from "../service/index";
export default {
    name: "Main",
    components: { Header, InputBox, Nav },

    data() {
        return {
            navList: [
                {
                    icon: "icon-zhineng",
                    type: "智能导诊",
                    path: "/guidance",
                    id: 0
                },
                {
                    icon: "icon-duihua",
                    type: "智能问病",
                    path: "/askingDisease",
                    id: 1
                },
                {
                    icon: "icon-yao",
                    type: "药物查询",
                    path: "drugInquiry",
                    id: 2
                },
                {
                    icon: "icon-fankui",
                    type: "意见反馈",
                    path: "/feedback",
                    id: 3
                }
            ],
            arr: [],
            navItem: {},
            age: 24,
            gender: "男",
            pageCount: 1,
            symptomId: "",
            inputText: "",
            offsetTop: 0,
            wechatConfigToken: "795ce1e2-c86a-48cf-8e0b-6155dba93b40"
        };
    },

    methods: {
        // 年龄性别变更
        ageChange(params) {
            this.age = params.age;
            this.gender = params.gender;
        },
        // 头部导航
        nav(item) {
            this.navItem = item;
            if (item.type != "意见反馈") {
                // this.arr = [];
                // this.$router.push(item.path);
                this._response(item, "");
            } else {
                window.location.href =
                    "http://res.zwjk.com/h5-test/open-application-front/prod/#/feedbackTypes?type=1&router=feedbackTypes&hospitalId=08adfbab-06f6-48df-9d3c-ed76e81bcb01&wechatConfigToken=f78c6ace-9942-4051-9d90-0c9ccab9f754&projectid=4933a927-fae0-4a77-8ead-dadf678af195&applicationId=3a385635-a88a-41a3-9ef7-a1254d1bg564";
            }
        },
        // 发送
        send(inputText) {
            this.inputText = inputText;
            if (inputText != "") {
                this.arr.push({
                    type: "C",
                    icon: "icon-tubiao_guke",
                    content: inputText
                });
                this.getHeight();
                this._getQueryType(inputText);
            }
        },
        // 自动应答
        _response(item, inputText) {
            let obj = autoResponse(item, inputText);
            this.arr.push({
                source: "U",
                icon: "icon-customer",
                content: obj
            });
            this.getHeight();
        },
        // 询问类型
        _getQueryType(inputText) {
            let params = {
                inputContent: inputText,
                age: this.age,
                gender: this.gender == "男" ? "M" : "F"
            };
            queryType(params).then(res => {
                if (res.data.type == 1) {
                    this.arr.push(
                        Object.assign(res.data, {
                            source: "U",
                            icon: "icon-customer",
                            tip: "您是否查找以下疾病？",
                            description: "diseaseList"
                        })
                    );
                    this.getHeight();
                } else if (res.data.type == 2) {
                    this._getMedicineList().then(res => {
                        let arr = res.data.result.filter((item,index)=>{
                            return index<20
                        })
                        this.arr.push(
                            Object.assign({ arr }, {
                                source: "U",
                                icon: "icon-customer",
                                tip: "您是否查找以下药物？",
                                description: "medicineList",
                                type: 2
                            })
                        );
                        this.getHeight();
                    });
                } else if(res.data.type == 3) {
                    this._getBodyList().then(res => {
                        this.arr.push(
                            Object.assign(res.data, {
                                source: "U",
                                icon: "icon-customer",
                                tip: "您身体哪个部位不舒服?",
                                description: "bodyList",
                                type: 3
                            })
                        );
                        this.getHeight();
                    });
                }else if (res.data.type == 4) {
                    let arr = res.data.result.bodySymptomList.filter(item => {
                        return item.haveQuestion == 1;
                    });
                    this.arr.push(
                        Object.assign(
                            { arr },
                            {
                                source: "U",
                                icon: "icon-customer",
                                tip: "您是否患有以下症状？",
                                description: "SymptomList"
                            }
                        )
                    );
                    this.getHeight();
                } 
            });
        },
        // 疾病详情
        _getDiseaseDetail(diseaseId) {
            queryDiseaseDetail({ diseaseId: diseaseId }).then(res => {
                // 获取T对应科室
                queryDeptName({
                    deptName: res.data.className,
                    wechatConfigToken: this.wechatConfigToken
                }).then(response => {
                    if (Object.keys(response.data).length) {
                        res.data.className = response.data.name;
                        this.$set(res.data, "deptId", response.data.id);
                        // 获取医生列表
                        queryDoctorList({
                            deptId: response.data.id,
                            wechatConfigToken: this.wechatConfigToken
                        }).then(resp => {
                            let respArr = resp.data;
                            this.arr.push(
                                Object.assign(
                                    res.data,
                                    { respArr },
                                    {
                                        source: "U",
                                        icon: "icon-customer",
                                        description: "diseaseDetail",
                                        type: 1
                                    }
                                )
                            );
                            this.getHeight();
                        });
                    } else {
                        this.arr.push(
                            Object.assign(res.data, {
                                source: "U",
                                icon: "icon-customer",
                                description: "diseaseDetail",
                                type: 1
                            })
                        );
                        this.getHeight();
                    }
                });
            });
        },
        // 身体部位
        _getBodyList() {
            let params = {
                pageNum: 1,
                pageSize: 20
            };
            return new Promise((resolve, reject) => {
                queryCheckBodyList(params).then(res => {
                    resolve(res);
                });
            });
        },
        // 症状列表
        _getSymptomList(id) {
            let params = {
                pageNum: 1,
                pageSize: 50,
                age: this.age,
                gender: this.gender == "男" ? "M" : "F",
                bodyId: id
            };
            querySymptomList(params).then(res => {
                let arr = res.data.bodySymptomList.filter(item => {
                    return item.haveQuestion == 1;
                });
                this.arr.push(
                    Object.assign(
                        { arr },
                        {
                            source: "U",
                            icon: "icon-customer",
                            tip: "您是否患有以下症状？",
                            description: "SymptomList"
                        }
                    )
                );
                this.getHeight();
            });
        },
        // 伴随症状
        _getCompanySymptom(id) {
            this.symptomId = id;
            let params = {
                pageNum: this.pageCount,
                pageSize: 10,
                gender: this.gender == "男" ? "M" : "F",
                symptomId: id
            };
            queryQuestionOptionList(params).then(res => {
                let arr = [];
                res.data.list &&
                    res.data.list.forEach(item => {
                        item.list1 &&
                            item.list1.forEach(i => {
                                arr.push(i);
                            });
                    });
                this.arr.push(
                    Object.assign(
                        { arr },
                        {
                            source: "U",
                            icon: "icon-customer",
                            tip: "您是否伴随以下症状？",
                            description: "acompanySymptom"
                        }
                    )
                );
                this.getHeight();
            });
        },
        // 获取疾病列表
        _getDisease(id) {
            let params = {
                age: this.age,
                gender: this.gender == "男" ? "M" : "F",
                symptomId: this.symptomId,
                optionId: id
            };
            queryDisease(params).then(res => {
                queryDeptName({
                    deptName: res.data.className,
                    wechatConfigToken: this.wechatConfigToken
                }).then(response => {
                    if (Object.keys(response.data).length) {
                        res.data.className = response.data.name;
                        this.$set(res.data, "deptId", response.data.id);
                        // 获取医生列表
                        queryDoctorList({
                            deptId: response.data.id,
                            wechatConfigToken: this.wechatConfigToken
                        }).then(resp => {
                            let respArr = resp.data;
                            this.arr.push(
                                Object.assign(
                                    res.data,
                                    { respArr },
                                    {
                                        source: "U",
                                        icon: "icon-customer",
                                        tip: "您可能患有以下疾病",
                                        description: "diseaseList"
                                    }
                                )
                            );
                            this.getHeight();
                        });
                    } else {
                        this.arr.push(
                            Object.assign(res.data, {
                                source: "U",
                                icon: "icon-customer",
                                tip: "您可能患有以下疾病",
                                description: "diseaseList"
                            })
                        );
                        this.getHeight();
                    }
                });
            });
        },
        // 问药
        _getMedicineList() {
            let params = {
                medicine: this.inputText
            };
            return new Promise((resolve, reject) => {
                queryMedicineList(params).then(res => {
                    resolve(res);
                });
            });
        },
        // 药物详情
        _getMedicineDetail(id) {
            let params = {
                medicineId: id
            };
            queryMedicineDetail(params).then(res => {
                this.arr.push(
                    Object.assign(res.data, {
                        source: "U",
                        icon: "icon-customer",
                        description: "medicineDetail",
                        type: 2
                    })
                );
                this.getHeight();
            });
        },
        // 获取高度
        getHeight() {
            let arr = document.getElementsByClassName("left-bubble");
            let height = arr[arr.length - 1].offsetTop;
            this.offsetTop = this.offsetTop + height;
            let top = this.offsetTop ? this.offsetTop : height;
            window.scrollTo({
                top: top - height,
                behavior: "smooth"
            });
        }
    },
    created() {
        this.nav(this.navList[0]);
    }
};



